.banner{
    background-image: url("../img/background.jpg.crdownload"); 
    background-repeat: no-repeat;
    background-size: cover;
    height:100vh;
    text-align:center;
    @include flex(
        $flex-display:flex,
        $flex-direction:column,
        $flex-wrap:wrap,
        $flex-align:normal,
        $flex-justify:space-between
        );
    margin-bottom:20px;
}



.intro{ 
    @extend %margin-menu;
}





#contact{
    @include media(x-large){
        height:100vw;
        padding-top: 20px;
    }
}